.PopupSaved {
    position: fixed;
    left: 5%;
    top: -10%;
    border: 1px solid rgb(48, 209, 48);
    background-color: rgb(48, 209, 48);
    color: white;
    font-weight: white;
    width: 150px;
    padding: 15px;
    height: 20px;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    border-radius: 10px;
}

.PopupDelete {
    position: fixed;
    left: 5%;
    top: -20%;
    border: 1px solid rgb(209, 47, 47);
    background-color: rgb(209, 47, 47);
    color: white;
    font-weight: white;
    width: 150px;
    padding: 15px;
    height: 20px;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    border-radius: 10px;
}

.Animation {
    -webkit-animation: Popup 2s ease-in;
            animation: Popup 2s ease-in;
}

@-webkit-keyframes Popup{
    0% {
        top: -20%;
    }
    25% {
        top: 9%; 
    }
    80% {
        top: 9%;
    }
    100% {
        top: -6%;
    }
}

@keyframes Popup{
    0% {
        top: -20%;
    }
    25% {
        top: 9%; 
    }
    80% {
        top: 9%;
    }
    100% {
        top: -6%;
    }
}



